import * as actionTypes from '../actions';
import CalculatorOperations from '../operations'

const initialState = {
    value: null,
    displayValue: '0',
    operator: null,
    waitingForOperand: false
};

const reducer = (state = initialState, action) => {
    let {
        value,
        operator,
        displayValue,
        waitingForOperand
    } = state;

    switch (action.type) {

        case actionTypes.INPUT_NUMBER:
            if (waitingForOperand) {
                displayValue = String(action.val);
                waitingForOperand = false;
            } else {
                displayValue = displayValue === '0' ? String(action.val) : displayValue + action.val;
            }
            return {
                value: value,
                displayValue: displayValue,
                operator: operator,
                waitingForOperand: waitingForOperand
            }

        case actionTypes.OPERATION:
            const inputValue = parseFloat(state.displayValue);
            if (state.value == null) {
                value = inputValue;
            } else if (state.operator) {
                const currentValue = state.value || 0;
                const newValue = CalculatorOperations[state.operator](currentValue, inputValue);
                value = newValue;
                if(!isFinite(newValue)){
                    displayValue='';
                }else{
                    displayValue = String(parseFloat(newValue).toPrecision(4));
                }
            }
            waitingForOperand = true;
            operator = action.val;
            return {
                value: value,
                displayValue: displayValue,
                operator: operator,
                waitingForOperand: waitingForOperand
            }

        case actionTypes.DOT_OPERATION:
            if (!(/\./).test(state.displayValue)) {
                displayValue = state.displayValue + '.';
                waitingForOperand = false;
            }
            return {
                value: value,
                displayValue: displayValue,
                operator: operator,
                waitingForOperand: waitingForOperand
            }

        case actionTypes.CLEAR:
            return {
                value: null,
                displayValue: '0',
                operator: null,
                waitingForOperand: false
            }

         default:
            return{...state}

    }
  
};

export default reducer;



